import { Component, OnInit } from '@angular/core';
import { ExpenseService } from './expense.service';
import { Expense, ExpenseSearchModel } from '../expense.models';
import { BsModalRef } from 'ngx-bootstrap';
import { AlertService } from 'ngx-alerts';
import { FormGroup, FormBuilder } from '@angular/forms';
import { cursorTo } from 'readline';

@Component({
  selector: 'app-expense-list',
  templateUrl: './expense-list.component.html'
})
export class ExpenseListComponent implements OnInit {
  expenses: Array<Expense> = [];
  deletingExpense: Expense;
  bsModalRef: BsModalRef;
  isCollapsed = false;

  searchForm: FormGroup;

  //#region searchModel
  private _searchModel: ExpenseSearchModel = null;
  get searchModel(): ExpenseSearchModel {
    if (!this._searchModel) {
      var sessionSM = JSON.parse(sessionStorage.getItem('expenseSearchModel'));
      if (sessionSM != null) {
        this._searchModel = sessionSM;
      } else {
        this._searchModel = new ExpenseSearchModel();
      }
    }
    return this._searchModel;
  }

  set searchModel(value: ExpenseSearchModel) {
    sessionStorage.setItem('expenseSearchModel', JSON.stringify(value))
    this._searchModel = value;
  }
//#endregion

  constructor(
    private formBuilder: FormBuilder,
    private expenseService: ExpenseService,
    private alertService: AlertService) {
  }

  ngOnInit() {
    this.buildForm(this.formBuilder);
    this.search();
  } 

  private buildForm(formBuilder: FormBuilder) {

    // // perpare expenseCategories
    // var expenseCategories = [];
    // if (this.searchModel.expenseCategories) {
    //   expenseCategories = this.searchModel.expenseCategories.map(x => {
    //     return { name: x.name }
    //   });
    // }

    this.searchForm = formBuilder.group({
      id: this.searchModel.id,
      name: this.searchModel.name,
      description: this.searchModel.description,
      amount: this.searchModel.amount,
      dateFrom: this.searchModel.dateFrom,
      dateTo: this.searchModel.dateTo,
      expenseTags: [this.searchModel.expenseTags,],
      expenseAccounts: [this.searchModel.expenseAccounts,],
      expenseCategories: [this.searchModel.expenseCategories,],
    });
  }

  search() {
    var modelToSubmit = this.searchForm.value;
    this.searchModel = modelToSubmit; // save searchModel in current session (before preparing)
    //modelToSubmit.expenseTags = modelToSubmit.expenseTags.map(x => x.name).join(",");   

    // prepare expenseTags
    if(modelToSubmit.expenseTags){
      modelToSubmit.expenseTags = modelToSubmit.expenseTags.map(x => x.name); //convert objects to string array e.g. ["car-gas", "car-tax"]
    }

     // prepare expenseAccounts
     if (modelToSubmit.expenseAccounts) { 
      modelToSubmit.expenseAccounts = modelToSubmit.expenseAccounts.map(x => x.name); //convert objects to string array e.g. ["Nasir private", "Nasir ABN Amro"]
    }

    // prepare expenseCategories
    if (modelToSubmit.expenseCategories) { 
      modelToSubmit.expenseCategories = modelToSubmit.expenseCategories.map(x => x.name); //convert objects to string array e.g. ["ATM", "Car"]
    }
    
    this.expenseService.search(modelToSubmit).subscribe(
      rez => this.expenses = rez.data,
      err => this.alertService.danger(err)
    )
  }

  onSubmit() {
    this.search();
  }

  getTotalAmout() {
    var sum = this.expenses.reduce((a, b) => a + b.amount, 0);
    return sum;
  }

  getSumPositive() {
    var positive = this.expenses.filter((a) => a.amount >= 0);
    var sumpositive = positive.reduce((a, b) => a + b.amount, 0);
    return sumpositive;
  }

  getSumNegative() {
    var negative = this.expenses.filter((a) => a.amount < 0)
    var sumNegative = negative.reduce((a, b) => a + b.amount, 0)
    return sumNegative;
  }

  public uploadFile(files) {
    if (files.length === 0) {
      return;
    }

    let fileToUpload = <File>files[0];
    this.expenseService.uploadFile(fileToUpload).subscribe(
      rez => this.search(),
      err => this.alertService.danger(err)
    )
  }
}
